.top-skills {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  margin: 0;
}

.skill {
  margin: 1em 3em;
}

/*skill percentage svg*/

.skills .skill #svg circle {
  stroke-dashoffset: 0;
  transition: stroke-dashoffset 10s linear;
  stroke: #676767;
  stroke-width: 0.5em;
}

.skills .skill #svg #bar {
  stroke: #648fcf;
}

.percentage {
  font-size: 200%;
  font-weight: lighter;
}

.info {
  text-align: center;
}
